import { useState, Fragment, useContext, useEffect } from "react"
import { useParams } from "react-router-dom"
import toast from "react-hot-toast"
import {
  Row,
  Col,
  Card,
  Form,
  CardBody,
  Button,
  Badge,
  Modal,
  Input,
  Label,
  ModalBody,
  ModalHeader,
  CardText
} from "reactstrap"
import { useTranslation } from "react-i18next"
import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import Moment from "moment"
import "moment/locale/id"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import { selectThemeColors } from "@utils"
import classnames from "classnames"

import Swal from "sweetalert2"
import Select from "react-select"
import withReactContent from "sweetalert2-react-content"
import { useForm, Controller } from "react-hook-form"
import { updateUser, getUser, getDataEmployee } from "../store"
import { getUnits } from "../../unit/store"
import { getData } from "../../roles-permissions/store/role"
import { useDispatch, useSelector } from "react-redux"

const MySwal = withReactContent(Swal)

const ModalAddEdit = ({ show, setShow, detailPage }) => {
  const [unitOptions, setUnitOptions] = useState([])
  const [unitOptionsSelected, setUnitOptionsSelected] = useState([])
  const [idRole, setIdRole] = useState(null)
  const [employeeOptionsSelected, setEmployeeOptionsSelected] = useState([])
  const [formEdited, setFormEdited] = useState(false)
  const { t } = useTranslation()
  // const [id, setId] = useState(null)
  let { id } = useParams()
  const dispatch = useDispatch()
  const store = useSelector((state) => state.users)
  const storeUnit = useSelector((state) => state.units)
  const storeRole = useSelector((state) => state.roles)
  const { colors } = useContext(ThemeColors)
  const [selectedUser, setSelectedUser] = useState(store.selectedUser)
  const {
    reset,
    control,
    setError,
    handleSubmit,
    formState: { errors }
  } = useForm({
    defaultValues: {
      username: selectedUser?.username,
      name: selectedUser?.name,
      phone: selectedUser?.phone ?? ""
    }
  })

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
        <CardText className="mb-0 mt-1 text-white">
          {t("Please Wait")}...
        </CardText>
      </Fragment>
    )
  }

  const handleChangeUnit = (e) => {
    // console.log(e)
    setUnitOptionsSelected(Array.isArray(e) ? e.map((x) => x.value) : [])
    // console.log(unitOptionsSelected)
  }

  const handleChangeEmployee = (e) => {
    // console.log(e)
    setEmployeeOptionsSelected(Array.isArray(e) ? e.map((x) => x.value) : [])
    // console.log(unitOptionsSelected)
  }

  const countChanges = (data) => {
    // console.log(data)

    const unitOptionsSelect =
      selectedUser.member_of !== null
        ? selectedUser?.member_of.map((item) => ({
            value: item.id,
            label: item.name
          }))
        : []

    // console.log(unitOptionsSelect)
    // console.log(unitOptionsSelected)
    const formEdited =
      selectedUser.name !== data.name ||
      selectedUser.username !== data.username ||
      (selectedUser.phone ?? "") !== data.phone ||
      JSON.stringify(unitOptionsSelect) !==
        JSON.stringify(unitOptionsSelected) ||
      idRole !== selectedUser.id_role
    setFormEdited(true)
    return formEdited
  }

  const onSubmit = (data) => {
    if (countChanges(data)) {
      // console.log(countChanges(data))
      if (
        Object.values(data).every((field) => {
          return field.length > 0 || field === ""
        })
      ) {
        if (data.phone === "") {
          delete data.phone
        }

        data["id"] = id ?? selectedUser.id
        data["id_role"] = idRole
        data["id_employee"] = employeeOptionsSelected?.id
        data["member_of"] = unitOptionsSelected
        dispatch(updateUser(data)).then((res) => {
          // console.log(res)
          if (res.meta.requestStatus === "fulfilled") {
            setShow(!show)
            toast.success(`${t("Successfully Updated Data")}`, {
              style: {
                padding: "16px",
                color: colors.success.main,
                border: `1px solid ${colors.success.main}`
              },
              iconTheme: {
                primary: colors.success.main
              }
            })
            if (detailPage) {
              dispatch(getUser(id))
            }

            // setUnitOptionsSelected(
            //   selectedUser.member_of.map((item) => ({
            //     value: item.id,
            //     label: item.name
            //   }))
            // )
          } else if (res.meta.requestStatus === "rejected") {
            toast.error(JSON.stringify(res.payload), {
              style: {
                padding: "16px",
                color: colors.danger.main,
                border: `1px solid ${colors.danger.main}`
              },
              iconTheme: {
                primary: colors.danger.main
              }
            })
          }
        })
      } else {
        for (const key in data) {
          if (data[key]?.length === 0) {
            setError(key, {
              type: "manual"
            })
          }
        }
      }
    } else {
      toast(`${t("You haven't changed anything")}`, { icon: "ðŸ‘" })
    }
  }

  const handleReset = () => {
    reset({
      username: selectedUser?.username,
      name: selectedUser?.name
    })
  }

  // ** Get data on mount
  useEffect(() => {
    // const { id } = useParams()
    // console.log(this.props.match.params.id)
    // if (!id) {
    //   id = idRow
    // }
    setSelectedUser(store.selectedUser)
    if (storeUnit.data.length === 0 && !storeUnit.isLoading) {
      dispatch(getUnits())
      setSelectedUser(store.selectedUser)
    }

    if (storeRole?.data.length === 0 && !storeRole.isLoading) {
      dispatch(getData())
    }

    if (store?.data_employee.length === 0 && !store.isLoading_employee) {
      dispatch(
        getDataEmployee({
          length: 200
        })
      )
      if (store.data_employee && selectedUser?.id_employee) {
        setEmployeeOptionsSelected(
          store.data_employee
            .filter((el) => el.id === selectedUser?.id_employee)
            .map((item) => ({
              value: item.id,
              label: `${item.FirstName} ${item?.LastName ?? ""}`
            }))
        )
      }
    }

    // console.log(storeUnit.data)
    // console.log(unitOptions)
    // console.log(unitOptionsSelected)

    setIdRole(selectedUser?.id_role)
  }, [])

  return (
    <Modal
      isOpen={show}
      toggle={() => setShow(!show)}
      className="modal-dialog-centered modal-lg"
    >
      <UILoader
        blocking={
          (store.isLoadingAddEdit || store.isLoadingDetail) &&
          !store.selectedUser
        }
        loader={<Loader />}
      >
        <ModalHeader
          className="bg-transparent"
          toggle={() => setShow(!show)}
        ></ModalHeader>
        <ModalBody className="px-sm-5 pt-50 pb-5">
          <div className="text-center mb-2">
            <h1 className="mb-1">
              {t("Edit")} {t("User Information")}
            </h1>
            <p>{t("Updating user details will receive a privacy audit.")}</p>
          </div>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row className="gy-1 pt-75">
              <Col md={12} xs={12}>
                <Label className="form-label" for="name">
                  {t("Full Name")}
                </Label>
                <Controller
                  defaultValue=""
                  control={control}
                  id="name"
                  name="name"
                  render={({ field }) => (
                    <Input
                      {...field}
                      id="name"
                      placeholder="John"
                      invalid={errors.name && true}
                    />
                  )}
                />
              </Col>

              <Col md={6} xs={12}>
                <Label className="form-label" for="username">
                  {t("Username")}:
                </Label>
                <Controller
                  defaultValue=""
                  control={control}
                  id="username"
                  name="username"
                  render={({ field }) => (
                    <Input
                      {...field}
                      id="username"
                      placeholder="john.doe.007"
                      invalid={errors.username && true}
                    />
                  )}
                />
              </Col>

              <Col md={6} xs={12}>
                <Label className="form-label" for="email">
                  {t("Email")}:
                </Label>
                <Input
                  type="email"
                  id="email"
                  disabled
                  defaultValue={selectedUser?.email}
                  placeholder="example@domain.com"
                />
              </Col>

              <Col md={6} xs={12}>
                <Label className="form-label" for="phone">
                  {t("Contact")}
                </Label>
                <Controller
                  defaultValue=""
                  control={control}
                  id="phone"
                  name="phone"
                  render={({ field }) => (
                    <Input
                      {...field}
                      value={selectedUser?.phone || ""}
                      id="phone"
                      placeholder="+62 813 940 10XX X"
                    />
                  )}
                />
              </Col>
              <Col md={6} xs={12}>
                <Label className="form-label" for="id_role">
                  {t("Role")}
                </Label>
                <Select
                  id="id_role"
                  theme={selectThemeColors}
                  defaultValue={storeRole.data
                    .filter((el) => el.id === selectedUser?.id_role)
                    .map((item) => ({
                      value: item.id,
                      label: item.name
                    }))}
                  onChange={(data) => {
                    setIdRole(data.value)
                  }}
                  options={storeRole.data.map((item) => ({
                    value: item.id,
                    label: item.name
                  }))}
                  className={classnames("react-select", {
                    "is-invalid": idRole === null
                  })}
                  classNamePrefix="select"
                />
              </Col>
              <Col md={12} xs={12}>
                <Label className="form-label" for="id_employee">
                  {t("Employee")}
                </Label>
                <Select
                  id="id_employee"
                  isClearable={true}
                  isLoading={store.isLoading_employee}
                  theme={selectThemeColors}
                  defaultValue={
                    store.data_employee
                      .filter((el) => el.id === selectedUser?.id_employee)
                      .map((item) => ({
                        value: item.id,
                        label: `${item.FirstName} ${item?.LastName ?? ""}`
                      })) ?? ""
                  }
                  onChange={(data) => {
                    handleChangeEmployee(data)
                  }}
                  options={store.data_employee.map((item) => ({
                    value: item.id,
                    label: `${item.FirstName} ${item?.LastName ?? ""}`
                  }))}
                  className="react-select"
                  classNamePrefix="select"
                />
              </Col>
              <Col md={12} xs={12}>
                <Label className="form-label" for="member_of">
                  {t("Unit")}
                </Label>
                <Select
                  id="member_of"
                  isClearable={false}
                  theme={selectThemeColors}
                  closeMenuOnSelect={false}
                  defaultValue={
                    store.selectedUser?.member_of
                      ? store.selectedUser?.member_of.map((item) => ({
                          value: item.id,
                          label: item.name
                        }))
                      : []
                  }
                  onChange={(data) => {
                    handleChangeUnit(data)
                  }}
                  isMulti
                  options={storeUnit.data
                    .map((item) => ({
                      value: item.id,
                      label: item.name
                    }))
                    .filter((x) => x.value !== "")}
                  className="react-select"
                  classNamePrefix="select"
                />
              </Col>
              <Col xs={12} className="text-center mt-2 pt-50">
                <Button type="submit" className="me-1" color="primary">
                  {t("Submit")}
                </Button>
                <Button
                  type="reset"
                  color="secondary"
                  outline
                  onClick={() => {
                    handleReset()
                    setShow(false)
                  }}
                >
                  {t("Cancel")}
                </Button>
              </Col>
            </Row>
          </Form>
        </ModalBody>
      </UILoader>
    </Modal>
  )
}

export default ModalAddEdit
